import { MantineTheme } from '@mantine/core';
import { ModalProps } from './Modal';

export class ModalComputer {
  private readonly closeButtonAlignment: 'start' | 'center' = 'start';
  private readonly headerBorder: boolean = false;

  public constructor(private readonly theme: MantineTheme, props: Pick<ModalProps, 'closeButtonAlignment' | 'headerBorder'>) {
    this.closeButtonAlignment = props.closeButtonAlignment ?? this.closeButtonAlignment;
    this.headerBorder = props.headerBorder ?? this.headerBorder;
  }

  public get closeButtonStyle() {
    return {    
      alignSelf: this.closeButtonAlignment === 'start' ? 'flex-start' : 'center',
    };
  }

  public get headerStyle() {
    return {
      borderBottom: this.headerBorder ? `1px solid ${this.theme.colors.gray[2]}` : 'none',
    };
  }
}
